.section-images {
  display: flex;
  width: 100%;
  gap: 2%;
  justify-content: center;
}
.section-images img {
  width: 32%;
}
@media (max-width: 575px) {
  .section-images {
    flex-wrap: wrap;
    gap: 20px 6%;
  }
  .section-images img:nth-of-type(1) {
    width: 47%;
  }
  .section-images img:nth-of-type(2) {
    width: 47%;
  }
  .section-images img:nth-of-type(3) {
    width: 100%;
  }
}

.section-images .images {
  width: 100%;
  height: 100%;
  gap: 31px 15px;
}
@media (min-width: 576px) and (max-width: 768px) {
  .section-images .images {
    gap: 9px;
  }
}
@media (max-width: 575px) {
  .section-images .images {
    gap: 13px;
  }
}
.section-images .images img:nth-of-type(1) {
  width: 24%;
}
@media (max-width: 575px) {
  .section-images .images img:nth-of-type(1) {
    width: 48%;
  }
}
.section-images .images img:nth-of-type(2) {
  width: 24%;
}
@media (max-width: 575px) {
  .section-images .images img:nth-of-type(2) {
    width: 48%;
  }
}
.section-images .images img:nth-of-type(3) {
  width: 24%;
}
@media (max-width: 575px) {
  .section-images .images img:nth-of-type(3) {
    width: 48%;
  }
}
.section-images .images img:nth-of-type(4) {
  width: 24%;
}
@media (max-width: 575px) {
  .section-images .images img:nth-of-type(4) {
    width: 48%;
  }
}
.section-images .images img:nth-of-type(5) {
  width: 32.5%;
}
@media (max-width: 575px) {
  .section-images .images img:nth-of-type(5) {
    width: 48%;
  }
}
.section-images .images img:nth-of-type(6) {
  width: 32.4%;
}
@media (max-width: 575px) {
  .section-images .images img:nth-of-type(6) {
    width: 48%;
  }
}
.section-images .images img:nth-of-type(7) {
  width: 32.4%;
}
@media (max-width: 575px) {
  .section-images .images img:nth-of-type(7) {
    width: 100%;
  }
}

